/*
  Styles for the projects section. Cards are arranged horizontally
  in a scrollable container with snap behaviour, making the
  collection easy to navigate on both desktop and mobile.
*/

.projects-section {
  margin-bottom: 80px;
}

.section-title {
  /* Increase project section titles for visual hierarchy */
  font-size: 40px;
  font-weight: 800;
  margin-bottom: 20px;
  /* Use Outfit for section titles to maintain consistency with the hero */
  font-family: 'Outfit', 'Inter', 'Poppins', sans-serif;
}

.projects-container {
  display: flex;
  flex-direction: column;
  gap: 28px;
}

.projects-container--showcase {
  gap: 24px;
}

/* Hide default scrollbar in WebKit browsers */
.projects-container::-webkit-scrollbar {
  height: 6px;
}

.projects-container::-webkit-scrollbar-thumb {
  background-color: var(--primary-color);
  border-radius: 3px;
}

.projects-container::-webkit-scrollbar-track {
  background-color: transparent;
}

.project-card {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 18px;
  padding: 28px;
  border-radius: 5px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  background: rgba(8, 8, 12, 0.65);
  backdrop-filter: blur(18px);
  transition: transform 0.3s ease, border-color 0.3s ease, background-color 0.3s ease;
}

.project-card:hover {
  border-color: rgba(255, 255, 255, 0.18);
  background: rgba(16, 16, 24, 0.72);
}

.projects-divider {
  width: 100%;
  height: 1px;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0));
  margin: 56px 0 32px;
}

.section-title--secondary {
  margin-bottom: 28px;
}

.project-card-header {
  display: flex;
  align-items: center;
  gap: 18px;
}

.project-card-heading {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.project-card-heading h3 {
  font-size: 22px;
  margin: 0;
  font-family: 'Outfit', 'Inter', 'Poppins', sans-serif;
  letter-spacing: 0.01em;
  color: var(--text-color, #ffffff);
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.project-role {
  font-weight: 600;
}

.project-company {
  font-size: 16px;
  font-weight: 400;
  color: var(--muted-text);
  letter-spacing: 0.04em;
  text-transform: uppercase;
}

.project-card .image-wrapper {
  flex-shrink: 0;
  width: 56px;
  height: 56px;
  border-radius: 5px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid rgba(255, 255, 255, 0.12);
  background: rgba(255, 255, 255, 0.08);
}

.project-card img {
  width: 100%;
  height: 100%;
}

.project-info h3 {
  font-size: 20px;
  margin-bottom: 4px;
  /* Project titles use Outfit for a strong display style */
  font-family: 'Outfit', 'Inter', 'Poppins', sans-serif;
}

.project-info {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.project-subtitle {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 15px;
  line-height: 1.6;
  color: var(--muted-text);
  font-family: 'Inter', 'Outfit', 'Poppins', sans-serif;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.project-subtitle li {
  margin: 0;
  padding-left: 18px;
  position: relative;
}

.project-subtitle li::before {
  content: '';
  position: absolute;
  left: 4px;
  top: 10px;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: currentColor;
  opacity: 0.35;
}

.project-subtitle-text {
  display: block;
  color: var(--muted-text);
  line-height: 1.6;
  font-size: 15px;
  font-weight: 400;
  text-align: justify;
  text-align-last: left;
  text-justify: inter-word;
  hyphens: auto;
}

.project-subtitle-highlight {
  color: var(--primary-color, currentColor);
  font-weight: 600;
}